<script>
import Header from "./Header";
import RjZsWi from "./RjZsWi";
import XFooter from "./XFooter";
import { taskStore } from "@/store/taskStore";

export default {
  name: "EventList",
  components: {
    Header,
    RjZsWi,
    XFooter,
  },
  props: ["rJZsWIProps"],
  data() {
    return {
      tabs: [" ", " ", " "],
      selectedTab: " ",
      currentPage: 1,
      itemsPerPage: 6,
      events: [],
      arrowLeftImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg",
      arrowRightImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg",
    };
  },
  created() {
    this.getEvents();
  },
  computed: {
    // filteredEvents() {
    //   if (this.selectedTab === " ") {
    //     return this.events;
    //   } else if (this.selectedTab === " ") {
    //     return this.events.filter((event) => event.type === "");
    //   } else if (this.selectedTab === " ") {
    //     return this.events.filter((event) => event.type === "");
    //   }
    // },
    // totalPages() {
    //   return Math.ceil(this.filteredEvents.length / this.itemsPerPage);
    // },
    // paginatedEvents() {
    //   const start = (this.currentPage - 1) * this.itemsPerPage;
    //   return this.filteredEvents.slice(start, start + this.itemsPerPage);
    // },

    // 24.12.19  -  :     
    visiblePages() {
      const totalPages = this.totalPages;
      const currentPage = this.currentPage;
      const maxVisiblePages = 5;

      let startPage = Math.max(1, currentPage - Math.floor(maxVisiblePages / 2));
      let endPage = startPage + maxVisiblePages - 1;

      if (endPage > totalPages) {
        endPage = totalPages;
        startPage = Math.max(1, endPage - maxVisiblePages + 1);
      }

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    // 24.11.22  -  
    totalPages() {
      return Math.ceil(this.filteredEvents(this.selectedTab).length / this.itemsPerPage);
    },
    paginatedEvents() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredEvents(this.selectedTab).slice(start, start + this.itemsPerPage);
    },
  },
  methods: {
    async getEvents() {
      try {
        //    .
        const response = await taskStore.getSelectTasks('events');
        console.log("Fetched events data:", response);

        //   events  
        this.events = response.map(item => ({
          id: item.id,
          description: item.title, // title description 
          period: `${item.startTime?.split('T')[0]} ~ ${item.endTime?.split('T')[0]}`, // startTime endTime  
          maskGroup: item.imageUrl, // imageUrl maskGroup 
          // type: item.status, //   ('', '' )
          // 24.11.22  - 
          startDate: new Date(item.startTime),
          endDate: new Date(item.endTime),
        }));

        // 24.11.22  - 
        // startDate  
        this.events.sort((a, b) => b.startDate - a.startDate);

        console.log("Processed events:", this.events);
      } catch (error) {
        console.error("Failed to fetch events data:", error);
      }
    },
    // 24.11.22  - 
    filteredEvents(tab) {
      const today = new Date();
      today.setHours(0, 0, 0, 0); //  00:00:00 

      return this.events.filter(event => {
        const startDate = new Date(event.startDate).setHours(0, 0, 0, 0);
        const endDate = new Date(event.endDate).setHours(0, 0, 0, 0);
        let type = "game";

        if (startDate <= today && today <= endDate) {
          type = "";
        } else if (endDate < today) {
          type = "";
        }

        event.type = type; //  

        // 24.11.26  - 
        //   startDate 
        if (startDate > today) return false;

        // 
        if (tab === " ") return true;
        if (tab === " " && event.type === "") return true;
        if (tab === " " && event.type === "") return true;

        return false;
      });
    },
    selectTab(tab) {
      this.selectedTab = tab;
      this.currentPage = 1; // Reset to the first page when tab is changed
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    goToPage(page) {
      this.currentPage = page;
    },

    toGoEventDetail(event) {
      console.log("Navigating to event detail with ID:", event); //  id 

      // 24.11.22 
      //     
      if (event.type === "") {
        alert(" .");
        return;
      }

      this.$router.push({
        path: "/event-detail",
        query: { id: event.id, type: event.type }
      });
    }
  },
};
</script>

