  },
  // 24.12.24  - 
  computed: {
   
    sortedRankingData() {
  if (!this.RankingData) return [];

  //   shot   
  const sortedData = this.RankingData.slice().sort((a, b) => b.shot - a.shot);

  let currentRank = 1;
  let previousShot = null; //  shot   
  let previousRank = 1; //  shot    

  //     
  sortedData.forEach((item) => {
    if (item.shot === previousShot) {
      //  shot  ,   
      item.rank = previousRank;
    } else {
      //  shot    
      item.rank = currentRank;
      previousRank = currentRank; //      previousRank 
    }

    previousShot = item.shot; //  shot   shot 
    currentRank++; //  
  });

  //  
  const start = (this.currentPage - 1) * this.itemsPerPage;
  const end = start + this.itemsPerPage;
  this.filteredItems = sortedData; //      
  return sortedData.slice(start, end); //     
},

    // sortedRankingData() {
