<script>
import { taskStore } from "@/store/taskStore"; // API 
import EditProfileModal from './EditProfileModal.vue';
export default {
  name: "EditProfile",
  components: {
    EditProfileModal
  },
  data() {
    return {
      isModalVisible: false,
      password: '',
      nickname: '',
      phoneNumber: '',
      email: '',
      nickError: null, // 24.12.05  - 
      pwError: null, // 24.12.05  - 
      phoneNmError: null, // 24.12.05  - 
      emailError: null, // 24.12.05  - 
      checkNick: false, // 24.12.05  - 
      checkPhoneNm: false, // 24.12.05  - 
    }
  },
  props: {
    loginInfo: {
      type: Object, //    
      required: true, //  prop 
    },
  },
  mounted() {
    this.nickname = this.loginInfo.userName
    this.password = this.loginInfo.storeId
    this.email = this.loginInfo.email
    this.phoneNumber = this.loginInfo.phoneNm
  },
  computed: {
    // 24.12.50  - 
    //    
    isFormValid() {
      return !this.nickError && !this.pwError && !this.phoneNmError && !this.emailError;
    },
  },
  methods: {
    editprofile() {
      try {
         // 24.12.05  - 
        if (!this.isFormValid) {
        alert("   .");
        return; //      
      }
      // 24.12.05  - 
        if (this.checkNick) {
        alert(" .");
        return; //      
      }
      // 24.12.05  - 
      if (this.checkPhoneNm) {
        alert(" .");
        return; //      
      }

        const params = {
          //   
          userName: this.nickname,
          storeId: this.password,
          phoneNumber: this.phoneNumber, //   'Male'  'Female' 
          email: this.email
        };
        console.log(" :", params); //   

        const response = taskStore.updateTask("v1/auth/me/update", params);
        alert("!");
      } catch (error) {
        console.error("  :", error);
        alert("  .");
      }
      // this.$router.replace({ path: "/myprofile" });
      this.$emit("confirm");
      // this.$router.push({ path: "/myprofile" });
    },
    closeModal() {
      this.isModalVisible = false;
    },
    // 24.11.15  - 
    closeEditModal() {
      // this.$router.replace({ path: "/myprofile" });
      this.$emit("close"); //   close  
    },
      // 24.12.05  - 
        validateNickname() {
      // 2~6, , ,  
      const regex = /^[a-zA-Z0-9-]{2,8}$/;
      this.nickError = !regex.test(this.nickname)
        ? " , ,   2~8  ."
        : null;
      },
          // 24.12.05  - 
          validatePassword() {
            const regex = /^\d{4}$/;
            this.pwError = !regex.test(this.password)
        ? "  4 ."
        : null;
      },
          // 24.12.05  - 
          validatePhoneNm() {
            const regex = /^\d{11}$/;
            this.phoneNmError = !regex.test(this.phoneNumber)
        ? "  11 ."
        : null;
      },
          // 24.12.05  - 
          validateEmail() {
            const regex = /^[^\u3131-\uD79D]+$/; //     
            this.emailError = !regex.test(this.email)
        ? "    ."
        : null;
      },

    // 24.12.05  - 
    nickBlur() {
      //       
      console.log('Blur  , nick:', this.nickname);
      this.checkingNick();
    },
            // 24.12.05  - 
    nickEnter() {
      // Enter     
      console.log('Enter  , nick:', this.nickname);
      this.checkingNick();
    },
      // 24.12.05  - 
    async checkingNick() {

    const nickDt = await taskStore.getSelectTasks(`v1/users/nick/${this.nickname}`);
    console.log("Nick   : : : ", nickDt)
    if (nickDt) {
      this.checkNick = true
    } else {
      this.checkNick = false
    }
    },
     // 24.12.05  - 
    handleBlur() {
      //       
      console.log('Blur  , :', this.phoneNumber);
      this.checkingPhoneNm();

    },
     // 24.12.05  - 
    handleEnter() {
      // Enter     
      console.log('Enter  , :', this.phoneNumber);
      this.checkingPhoneNm();

    },
       // 24.12.05  - 
    async checkingPhoneNm() {
      // users/phone/1
      const signDt = await taskStore.getSelectTasks(`v1/users/phone/${this.phoneNumber}`);
      console.log("   : : : ", signDt)
      if (signDt) {
        this.checkPhoneNm = true
      } else {
        this.checkPhoneNm = false
      }
    },
  }
};
</script>


