<template>
  <div class="container-center-horizontal">
    <div class="signup2-2 screen">
      <Header />
      <div class="signup2-wrap">
        <h1 class="signup2-title valign-text-middle pretendard-extra-extra-bold-black-30px"></h1>
        <div class="signup2-content">
          <div class="signup2-content-list">
            <div class="signup2-content-input-align">
              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px"> &nbsp;&nbsp;<p style="color: red;">*</p></div>
                <input type="text" v-model="name" placeholder=" "
                  class="signup2-content-item-input-box pretendard-medium-mountain-mist-16px">
              </div>
              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px">&nbsp;&nbsp;<p style="color: red;">*</p></div>
                <!-- 24.11.14  - signup2-input-align  -->
                <div class="signup2-input-align">
                  <input type="text" v-model="id" @input="validateId" :maxlength="12" @blur="handleBlur"
                    @keyup.enter="handleEnter" placeholder=", / 6 ~ 12 "
                    class="signup2-content-item-input-box valign-text-middle pretendard-medium-mountain-mist-16px">
                  <!-- 24.11.14  -  -->
                  <div class="error-message pretendard-regular-normal-white-12px" v-if="idError">{{ idError }}</div>
                  <div class="error-message pretendard-regular-normal-white-12px" v-else-if="checkId">  .</div>
                  <div class="error-message pretendard-regular-normal-white-12px" style="color: #184199" v-else-if="!idError && !checkId">  .</div>

                </div>
              </div>
              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px">&nbsp;&nbsp;<p style="color: red;">*</p></div>
                <!-- 24.11.14  - signup2-input-align  -->
                <div class="signup2-input-align">
                  <input type="password" v-model="pw" @input="validatePassword" :maxlength="20" placeholder=" "
                    class="signup2-content-item-input-box valign-text-middle pretendard-medium-mountain-mist-16px">
                  <!-- 24.11.14  -  -->
                  <div class="error-message pretendard-regular-normal-white-12px" v-if="pwError">{{ pwError }}</div>
                </div>
              </div>
              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px"> &nbsp;&nbsp;<p style="color: red;">*</p></div>
                <!-- 24.11.14  - signup2-input-align  -->
                <div class="signup2-input-align">
                  <!-- 24.11.14  - v-model  -->
                  <input type="password" v-model="confirmPw" :maxlength="20" placeholder=" "
                    class="signup2-content-item-input-box valign-text-middle pretendard-medium-mountain-mist-16px">
                  <!-- 24.11.14  -  -->
                  <div class="error-message pretendard-regular-normal-white-12px" v-if="confirmPwError">{{ confirmPwError }}</div>
                </div>
              </div>

              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px">&nbsp;&nbsp;<p style="color: red;">*</p></div>
                <input type="date" v-model="yymmdd"
                  class="signup2-content-item-input-box pretendard-medium-mountain-mist-16px">
              </div>
              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px">&nbsp;&nbsp;<p style="color: red;">*</p></div>
                <div class="gender">
                  <label>
                    <input type="radio" name="gender" class="pretendard-medium-mountain-mist-16px" value="MALE"
                      v-model="gender" />
                    
                  </label>
                  <label style="margin-left:20px;">
                    <input type="radio" name="gender" class="pretendard-medium-mountain-mist-16px" value="FEMALE"
                      v-model="gender" />
                    
                  </label>
                </div>
              </div>
              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px">&nbsp;&nbsp;<p style="color: red;">*</p></div>
                <!-- 24.11.14  - signup2-input-align  -->
                <div class="signup2-input-align">
                  <input type="text" placeholder=" " v-model="nick" @input="validateNickname" :maxlength="8"
                    @blur="nickBlur" @keyup.enter="nicknter"
                    class="signup2-content-item-input-box valign-text-middle pretendard-medium-mountain-mist-16px">
                  <!-- 24.11.14  -  -->
                  <div class="error-message pretendard-regular-normal-white-12px" v-if="nickError">{{ nickError }}</div>
                  <div class="error-message pretendard-regular-normal-white-12px" v-else-if="checkNick">  .</div>
                  <div class="error-message pretendard-regular-normal-white-12px" style="color: #184199" v-else-if="nick.length > 1 && !checkNick"> 
                    .</div>
                </div>
              </div>

              <div class="signup2-content-item">
                <div class="signup2-content-item-title pretendard-medium-log-cabin-18px">&nbsp;&nbsp;<p style="color: red;">*</p></div>
                <input type="text" v-model="email" placeholder=" "
                  class="signup2-content-item-input-box valign-text-middle pretendard-medium-mountain-mist-16px">
              </div>
            </div>
            <p class="signup2-waring-text valign-text-middle pretendard-regular-normal-mine-shaft-14px">  
              
                 ,     .</p>
          </div>
          <button class="signup-btn pretendard-semi-bold-white-18px" @click="submitsignup()"
            :disabled="!isFormValid"></button>
        </div>
      </div>
      <XFooter />
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import XFooter from "./XFooter";
import { taskStore } from '@/store/taskStore';  // API 
import axios from 'axios';


export default {
  name: "SignUp",
  components: {
    Header,
    XFooter,
  },

  data() {
    return {
      signDt: [],
      name: '',
      id: '',
      pw: '',
      confirmPw: "",// 24.11.14 
      yymmdd: '',
      gender: '',
      nick: '',
      email: '',
      idError: null, // 24.11.14 
      pwError: null, // 24.11.14 
      confirmPwError: null, // 24.11.14 
      nickError: null, // 24.11.14 
      checkId: false,
      checkNick: false
    }
  },
  mounted() {
    const phoneNo = localStorage.getItem("phoneNo");
    if (phoneNo) {
     this.id = phoneNo;
    } else {
     alert('   .')
     this.$router.push({ path: "/phone-certification" })
    }
  },
  computed: {
    // 24.11.14  - 
    //    
    isFormValid() {
      return !this.idError && !this.pwError && !this.confirmPwError && !this.nickError;
    },
  },
  watch: {
    // 24.11.14  - 
    confirmPw(value) {
      if (value !== this.pw) {
        this.confirmPwError = "  .";
      } else {
        this.confirmPwError = null;
      }
    },
  },
  methods: {
    nickBlur() {
      //       
      console.log('Blur  , nick:', this.nick);
      this.checkingNick();
    },
    nickEnter() {
      // Enter     
      console.log('Enter  , nick:', this.nick);
      this.checkingNick();
    },
    handleBlur() {
      //       
      console.log('Blur  , :', this.id);
      this.checkingId();

    },
    handleEnter() {
      // Enter     
      console.log('Enter  , :', this.id);
      this.checkingId();

    },
    async checkingId() {
      // users/phone/1

      const signDt = await taskStore.getSelectTasks(`v1/users/phone/${this.id}`);
      console.log("   : : : ", signDt)
      if (signDt) {
        this.checkId = true
      } else {
        this.checkId = false
      }


    },
    async checkingNick() {

      const nickDt = await taskStore.getSelectTasks(`v1/users/nick/${this.nick}`);
      console.log("Nick   : : : ", nickDt)
      if (nickDt) {
        this.checkNick = true
      } else {
        this.checkNick = false
      }
    },
    // 24.11.15  - 
    checkPasswordMatch() {
      this.confirmPwError = this.pw !== this.confirmPw
        ? "  ."
        : null;
    },
    // 24.11.15  - 
    validateId() {
      // 6~12,    
      const regex = /^[a-z0-9]{6,12}$/;
      this.idError = !regex.test(this.id)
        ? "    6~12  ."
        : null;
    },
    // 24.11.15  - 
    validatePassword() {
      //  8~20,     
      const regex = /^(?!.*[^a-zA-Z0-9])(?!.*(.)\1{2,}).{8,20}$/;
      this.pwError = !regex.test(this.pw)
        ? " 8~20         ."
        : null;
    },
    // 24.11.15  - 
    validateNickname() {
      // 2~6, , ,  
      const regex = /^[a-zA-Z0-9-]{2,8}$/;
      this.nickError = !regex.test(this.nick)
        ? " , ,   2~8  ."
        : null;
    },
    async submitsignup() {

      if (!this.isFormValid) {
        alert("   .");
        return; //      
      }
      if (this.checkId) {
        alert(" .");
        return; //      
      }
      if (this.checkNick) {
        alert(" .");
        return; //      
      }

      const phoneNo = localStorage.getItem("phoneNo");
      const storeId = phoneNo.slice(-4);
      // http://localhost:3000/api/v1/auth/email/register
      //http://localhost:3000/api/v1/auth/email/register
      var params = {
        "name": this.name,
        "phoneNumber": this.id,
        "password": this.pw,
        "birthDate": this.yymmdd,
        "gender": this.gender,
        "phoneNm": phoneNo,
        "storeId": storeId,
        "userName": this.nick,
        "email": this.email,
        "termsOfServiceAgreed": true,
        "marketingAgreement": false,
        "accountType": "PHONE",
        "socialId": "12345678901234567890"
      };
      //                   {
      //   "name": "John Doe",
      //   "phoneNumber": "01012345678",
      //   "password": "Password123!",
      //   "birthDate": "1990-01-01",
      //   "gender": "MALE",
      //   "email": "user@example.com",
      //   "termsOfServiceAgreed": true,
      //   "marketingAgreement": false,
      //   "accountType": "PHONE",
      //   "socialId": "12345678901234567890"
      // }
      // this.signDt = await taskStore.addTask('/auth/email/register',params);
      console.log("params : : : ", JSON.stringify(params))
      try {
        const response = await axios.post('https://13.124.62.68:3000/api/v1/auth/phone/register',
          params,
          {
            headers: {
              'Content-Type': 'application/json'
            }
          }
        )
        // console.log(me.data.message)
        console.log("this.signDt : : : : : : ", JSON.stringify(response))
        // localStorage.setItem('signDt', JSON.stringify(re));
        // localStorage.setItem('Token', JSON.stringify(response.token));
        if (response) {
          alert(" ")
          //    token  
          console.log("this.signDt : : : : : : ", JSON.stringify(response.config.data)) //    
          this.$router.push({ path: "/" })
        } else {
          alert("  .")
        }
      } catch (error) {
        alert("    .");
      }
    }
  }
};
</script>

<style scoped src="@/style/signup/signup.css" />