<script>
import ManagerHeader from "./ManagerHeader.vue";
import ManagerNavbar from "./ManagerNavbar.vue";
import PointChargeModal from "./PointChargeModal.vue";
import ManagerUsingCostModal from "./ManagerUsingCostModal.vue";
import { managerStore } from '@/store/managerStore';  // API 
import AddPaymentModal from "./AddPaymentModal.vue"; // 24.12.18  - 

export default {
  name: "ManagerPointManagement",
  components: {
    ManagerHeader,
    ManagerNavbar,
    PointChargeModal,
    ManagerUsingCostModal,
    AddPaymentModal // 24.12.18  - 
  },
  data() {
    return {
      selectedMny : 1000,
      selectedTab: "",
      selectedPlan: "",
      // 24.12.04  - 
      pricingPlan: '',
      // tabs: ["", "", "", ""],
      // 24.12.04 
      tabs: [""],
      // 24.12.04  - 
      startDate: "",
      endDate: "",
      totalPoint: 1000, //   ( )
      isModalVisible: false,
      isModalCVisible: false,
      isAddModalVisible: false, // 24.12.18  - 
      point: [
        { number: 6, pointTransaction_description: " ", pointTransaction_type: "", pointTransaction_amount: "+30,000", pointTransaction_balance: "70,000", pointTransaction_timestamp: "2024-06-25 23:59:59" },
        { number: 5, pointTransaction_description: "9  ", pointTransaction_type: "", pointTransaction_amount: "-30,000", pointTransaction_balance: "120,000", pointTransaction_timestamp: "2024-06-25 23:59:59" },
        { number: 4, pointTransaction_description: " ", pointTransaction_type: "", pointTransaction_amount: "+30,000", pointTransaction_balance: "170,000", pointTransaction_timestamp: "2024-06-25 23:59:59" },
        { number: 3, pointTransaction_description: "9  ", pointTransaction_type: "", pointTransaction_amount: "-30,000", pointTransaction_balance: "50,000", pointTransaction_timestamp: "2024-06-25 23:59:59" },
        { number: 2, pointTransaction_description: "9  ", pointTransaction_type: "", pointTransaction_amount: "+30,000", pointTransaction_balance: "70,000", pointTransaction_timestamp: "2024-06-25 23:59:59" },
        { number: 1, pointTransaction_description: "9  ", pointTransaction_type: "", pointTransaction_amount: "+30,000", pointTransaction_balance: "70,000", pointTransaction_timestamp: "2024-06-25 23:59:59" }
      ],
      arrowLeftImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg", //    URL
      arrowRightImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg", //    URL
      currentPage: 1,
      itemsPerPage: 5, // Display 6 courses per page
      pricePer9Holes : 1000,
      pricePerPerson : 1000
    };
  },
  async created() {
    const managerInfo = JSON.parse(localStorage.getItem('managerInfo'));
    console.log("managerInfo.id : : : : ", managerInfo.id);
    console.log("managerInfo.pricingPlan : : : : ", managerInfo.pricingPlan);

    // pricingPlan  
    this.pricingPlan = managerInfo.pricingPlan;
    this.pricePer9Holes = managerInfo.pricePer9Holes;
    this.pricePerPerson = managerInfo.pricePerPerson;
    // this.pricingPlan = "";

    //   
    const response = await managerStore.getTask(`points/admin/${managerInfo.id}`);
    console.log("roomNumbering : : : : : : ", JSON.stringify(response));
    this.point = response;
  },
  computed: {
    // 24.12.09  -  :  
    today() {
      const date = new Date();
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0'); //  0 
      const day = String(date.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    // 24.12.04  -  :     
    visiblePages() {
      const totalPages = this.totalPages;
      const currentPage = this.currentPage;
      const maxVisiblePages = 5;

      let startPage = Math.max(1, currentPage - Math.floor(maxVisiblePages / 2));
      let endPage = startPage + maxVisiblePages - 1;

      if (endPage > totalPages) {
        endPage = totalPages;
        startPage = Math.max(1, endPage - maxVisiblePages + 1);
      }

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    totalPages() {
      return Math.ceil(this.filteredItems.length / this.itemsPerPage);
    },
    paginatedItems() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredItems.slice(start, start + this.itemsPerPage);
    },
    // 24.12.04  - 
    filteredItems() {
      const filteredPoints = this.point.filter(point => {
        const matchesDateRange = this.isWithinDateRange(point.pointTransaction_timestamp);

        return matchesDateRange;
      });

      return filteredPoints;
    },
  },
  mounted() {
    this.getPointList()
  },
  methods: {
    // 24.12.04  - 
    isWithinDateRange(createdAt) {
      if (!this.startDate && !this.endDate) return true;

      const createdDate = new Date(createdAt.split("T")[0]);
      console.log("createdDate : : :", createdDate)
      const start = this.startDate ? new Date(this.startDate) : null;
      const end = this.endDate ? new Date(this.endDate) : null;

      return (!start || createdDate >= start) && (!end || createdDate <= end);
    },
    // 24.12.04  - 
    validateStartDate() {
      // 24.12.09  - today 
      const today = new Date();
      if (new Date(this.startDate) > today) {
        alert("     .");
        this.startDate = ""; // 
      }
      //       
      else if (this.endDate && new Date(this.startDate) > new Date(this.endDate)) {
        alert("      .");
        this.startDate = ""; // 
      }
      this.selectedTab = ""; //   
    },
    // 24.12.04  - 
    validateEndDate() {
      // 24.12.09  - today 
      const today = new Date();
      if (new Date(this.endDate) > today) {
        alert("     .");
        this.endDate = ""; // 
      }
      //       
      else if (this.startDate && new Date(this.endDate) < new Date(this.startDate)) {
        alert("      .");
        this.endDate = ""; // 
      }
      this.selectedTab = ""; //   
    },
    selectTab(tab) {
      this.selectedTab = tab;
      console.log('Selected Tab:', tab); //    .
      // 24.12.04  - 
      if (tab === "") {
        //    
        this.startDate = "";
        this.endDate = "";
        console.log('Date filters reset'); //    .
      }
    },
    // 24.12.04  -  :    
    formatAmount(amount) {
      return Number(amount).toLocaleString(); //  
    },
    async getPointList() {

      const managerInfo = JSON.parse(localStorage.getItem('managerInfo'));
      console.log("managerInfo.id : : : : ", managerInfo.id)
      console.log("managerInfo.pricingPlan : : : : ", managerInfo.pricingPlan)

      const response = await managerStore.getTask(`points/admin/${managerInfo.id}`);
      console.log("roomNumbering : : : : : : ", JSON.stringify(response))
      this.point = response
      // alert("    .")
      // this.getReservationDt(this.selectedDate);
    },
    goPayment(paymentInfo) {
      const managerInfo = JSON.parse(localStorage.getItem('managerInfo'));
      this.isAddModalVisible = false;
      console.log("  : : :  ");
      console.log('Payment Info:', paymentInfo);
      paymentInfo.managerid = managerInfo.id;

      this.$router.push({
            name: 'nicePays',
            query: paymentInfo,
        });
      // this.$router.push('/nicepays')
    },
    isPositiveMoney(money) {
      return money.startsWith("-");
    },
    selectPlan(plan) {
      this.selectedPlan = plan; //   
    },
    pointChargeModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
    // 24.12.18  - 
    openAddPaymentModal() {
      this.isAddModalVisible = true;
    },
    // 24.12.18  - 
    closeAddPaymentModal() {
      this.isAddModalVisible = false;
    },
    openCostModal() {
      this.isModalCVisible = true;
    },
    closeCModal() {
      this.isModalCVisible = false;
    },
    confirmPay(selected){
      this.isModalCVisible = false;
      console.log('selected Mny:', selected);
      this.selectedMny = selected.replace(/,/g, "");
      this.totalPoint = selected.replace(/,/g, "");

    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    goToPage(page) {
      this.currentPage = page;
    },
  },
};
</script>

