<script>
import AdminHeader from "./AdminHeader.vue";
import AdminNavbar from "./AdminNavbar.vue";
import { adminStore } from "@/store/adminStore";

export default {
  name: "AdminASManagementDetail",
  components: {
    AdminHeader,
    AdminNavbar,
  },
  data() {
    return {
      id: null,
      title: "",
      content: "",
      contents: "",
      requestedAt: "",
      selectedStatus: "",
      comments: [],
    };
  },
  async mounted() {
    //  id 
    this.id = this.$route.query.id;
    console.log(" ID:", this.id);
    await this.getASDetail();
  },
  methods: {
    statusClass(status) {
      if (status === "") return "status-reception";
      if (status === "") return "status-processing";
      if (status === "") return "status-hold";
      if (status === "") return "status-complete";
      return "";
    },
    async getASDetail() {
      try {
        // A/S   
        const response = await adminStore.getTask(`as-requests/${this.id}`);
        console.log("A/S  :", response);

        //  data  
        this.title = response[0].asRequest_title;
        (this.content = response[0].asRequest_description),
          (this.requestedAt = response[0].asRequest_requestedAt); //  
        this.selectedStatus = response[0].asRequest_status;

        // this.comments = response[0].commentList || [];
        // this.comments = this.comments.map(comment => ({
        //   ...comment,
        //   isEditing: false,
        //   editedContent: comment.comment_content
        // }));

        // 24.12.04  -  null   
        this.comments = (response[0].commentList || []).filter(
          (comment) =>
            comment.comment_content !== null &&
            comment.comment_content.trim() !== "" &&
            comment.comment_author !== null
        ).map((comment) => ({
          ...comment,
          isEditing: false,
          editedContent: comment.comment_content
        }));
      } catch (error) {
        console.error("A/S      :", error);
      }
    },
    toGoList() {
      this.$router.push({ path: "/adminas-management" });
    },
    async updateAnswer() {
      console.log(" :", this.selectedStatus);
      const params = {
        status: this.selectedStatus,
      };
      try {
        const response = await adminStore.updateTask(
          `as-requests/${this.id}`,
          params
        );
        console.log(" :", response);
        alert(" .");
      } catch (error) {
        console.error("   :", error);
        alert("   .");
      }
    },
    async postComments() {
      try {

        const now = new Date();
        const koreaTime = new Date(now.getTime() + 9 * 60 * 60 * 1000); // UTC+9  
        const createdAt = koreaTime
          .toISOString()
          .replace("T", " ")
          .slice(0, 19);
        // POST    
        const params = {
          // title: this.title,
          content: this.contents,
          author: "",
          category: "as",
          views: 0,
          freeBoardId: "1",
          userId: "1",
          afterServiceId: this.id,
          askId: "1",
          createdAt: createdAt,
          commentText: "0",
          updatedAt: createdAt,
        };
        console.log(" :", JSON.stringify(params, null, 2));

        const response = await adminStore.postTask("comments", params);

        console.log(" :");
        alert(" ");
        await this.getASDetail();
        this.contents = ""; //   
      } catch (error) {
        console.error(" :", error);
        alert(" .");
      }
    },
    toggleEditComment(comment) {
      if (comment.isEditing) {
        //     `updateComment` 
        this.updateComment(comment);
      } else {
        //        `editedContent` 
        comment.editedContent = comment.comment_content;
      }
      comment.isEditing = !comment.isEditing;
    },
    async updateComment(comment) {
      try {
        const now = new Date();
        const koreaTime = new Date(now.getTime() + 9 * 60 * 60 * 1000); // UTC+9  
        const updatedAt = koreaTime.toISOString().replace("T", " ").slice(0, 19);

        const params = {
          content: comment.editedContent,
          updatedAt: updatedAt,
        };

        //   
        const response = await adminStore.updateTask(`comments/${comment.comment_id}`, params);
        console.log("  :", response);

        // if (response.success) {
        //   alert(" .");
        //   comment.comment_content = comment.editedContent; //   
        //   comment.isEditing = false; //   
        // } else {
        //   alert("  .");
        // }
        await this.getASDetail();
      } catch (error) {
        console.error("    :", error);
        alert("    .");
      }
    }
    ,
    async deleteComments(commentId) {
      try {
        //   
        const response = await adminStore.deleteTask(`comments/${commentId}`);
        console.log("  :", response);

        //     
        alert(" .");
        await this.getASDetail();
        window.location.reload();
      } catch (error) {
        console.error("    :", error);
        alert("  .");
      }
    },
  },
};
</script>

