                      <!-- 24.12.04  -  -->
                      <div v-for="page in visiblePages" :key="page" :class="[
                        'page-number',
                        { active: page === currentPage },
                      ]" @click="goToPage(page)">
                        {{ page }}
                      </div>
                    </div>
                    <img class="pagination-arrow flipped" @click="nextPage" :src="arrowRightImage" :alt="'Next'"
                      :class="{ disabled: currentPage === totalPages }" />
