          <!--  -->
          <!-- 24.11.13  -font style, align  -->
          <div class="frame-7859-3">
            <!-- 24.12.20  -   -->
            <button class="cancel-btn pretendard-medium-white-18px" @click="cancel"></button>
            <!-- 24.12.20  -  , class   -->
            <button class="submit-btn pretendard-medium-white-18px" @click="postContactDt"></button>
          </div>
        </div>
      </div>
      <x-footer />
    </div>
  </div>
