<template>
  <div class="modal-overlay" @click.self="closeModal">
    <div class="modal-content">
      <!-- 24.12.11  -   -->
      <div class="title valign-text-middle notosanskr-medium-log-cabin-16px"> </div>
      <div>
        <!-- 24.12.11  -   -->
        <button
          class="blue-button pretendard-semi-bold-black-14px"
          :class="{ active: selected === '' }"
          @click="selectedStatus('')"
        >
          
        </button>
        <!-- 24.12.11  -   -->
        <button
          class="confirm-button pretendard-semi-bold-black-14px"
          :class="{ active: selected === '' }"
          @click="selectedStatus('')"
        >
          
        </button>
        <!-- 24.12.11  -   -->
        <button
          class="grey-button pretendard-semi-bold-black-14px"
          :class="{ active: selected === '' }"
          @click="selectedStatus('')"
        >
          
        </button>
      </div>

      <!-- <h2 class="pretendard-bold-mine-shaft-18px">
         : {{ selected }}
      </h2> -->
      <div class="form-group buttons">
        <!-- 24.12.11  -   -->
        <button class="cancel-btn notosanskr-light-white-16px" @click="closeModal"></button>
        <button class="confirm-btn notosanskr-light-white-16px" @click="confirmModal"></button>
      </div>
    </div>
  </div>
</template>
<script>
import { managerStore } from "@/store/managerStore"; // API 

export default {
  name: "ManagerReservationModal",
  data() {
    return {
      selected: "", //  
      values: "", // API  
    };
  },
  props: {
    reserId: {
      type: Number,
      required: true, //  ID 
    },
    reserStatus: {
      type: String,
      required: true, //    
    },
  },
  mounted() {
    //    
    this.selected = this.reserStatus;
    if (this.selected === "confirm") {
      this.selected = "";
    } else if (this.selected === "cancel") {
      this.selected = "";
    } else {
      this.selected = "";
    }
  },
  methods: {
    selectedStatus(status) {
      this.selected = status;
    },
    closeModal() {
      this.$emit("close");
    },
    async confirmModal() {
      try {
        // API   
        this.values = this.selected === "" ? "confirm" : "cancel";
        const params = { status: this.values };

        console.log(" :", params);

        // API 
        await managerStore.updateTask(`reservations/${this.reserId}/status`, params);

        console.log("API  ,   .");
        this.$emit("confirm",{status:this.selected});
        alert(`${this.selected}  .`);
      } catch (error) {
        console.error("API    :", error);
        alert("     .  .");
      }
    },
  },
};
</script>

