<script>
import ResetPasswordConfirmModal from "./ResetPasswordConfirmModal";
import axios from "axios";
import { taskStore } from "@/store/taskStore"; // API 

export default {
  name: "EditPassword",
  components: {
    ResetPasswordConfirmModal,
  },
  props: {
    phone: String, // phone props 
  },
  data() {
    return {
      isModalVisible: false,
      currentPassword: "", //  
      newPassword: "", //  
      confirmNewPassword: "", //   
      errorMessage: "",
      pwCheckMsg: "",
      chechkingPw: false,
      pwError: null, // 24.12.05 
      confirmPwError: null, // 24.12.05 
    };
  },
  mounted() {
    //     
    console.log(" phone :", this.phone);
  },
  watch: {
    // 24.12.04  - 
    confirmNewPassword(value) {
      if (value !== this.newPassword) {
        this.confirmPwError = "  .";
      } else {
        this.confirmPwError = null;
      }
    },
  },
  methods: {
    // 24.12.04  - 
    validatePassword() {
      //  8~20,     
      const regex = /^(?!.*[^a-zA-Z0-9])(?!.*(.)\1{2,}).{8,20}$/;
      this.pwError = !regex.test(this.newPassword)
        ? " 8~20         ."
        : null;
    },
    passwdBlur() {
      //       
      console.log('Blur  , nick:', this.currentPassword);
      this.chekPw(this.currentPassword);
    },
    passwdEnter() {
      // Enter     
      console.log('Enter  , nick:', this.currentPassword);
      this.chekPw(this.currentPassword);
    },
    async chekPw(password) {

      // v1/auth/me/password/gooara123
      const response = await taskStore.getSelectTasks(
        `v1/auth/me/password/${password}`
      );
      console.log("   :", response.success);
      if (response.success === true) {
        this.pwCheckMsg = ' .'
        this.chechkingPw = true
      } else {
        this.pwCheckMsg = '  .'
        this.chechkingPw = false
      }
    },
    //    
    async changePassword() {
      //  
      if (this.newPassword !== this.confirmNewPassword) {
        this.errorMessage = "   .";
        return;
      }
      if (this.chechkingPw === false) {
        alert("   .")
        return;
      }

      try {
        const token = localStorage.getItem("Token");
        if (!token) {
          alert(" .");
          return;
        }

        //    
        const response = await axios.post(
          "https://13.124.62.68:3000/api/v1/auth/reset-password",
          {
            phone: this.phone,
            newPassword: this.newPassword,
            confirmNewPassword: this.confirmNewPassword,
          },
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${token}`,
            },
          }
        );

        //  
        if (response.status === 200) {
          console.log("  :", response.data);
          alert("  .");
          this.showModal();
        } else {
          alert("  .");
        }
      } catch (error) {
        console.error("    :", error);
        alert("    .");
      }
    },
    closeModal() {
      this.$emit("close");
    },
    clearError() {
      this.errorMessage = "";
    },
    //  / 
    showModal() {
      this.isModalVisible = true;
    },
    closeAllModals() {
      this.isModalVisible = false;
      this.closeModal();
    },

  },
};
</script>

