<script>
import { taskStore } from "@/store/taskStore"; // API 

import Header from "./Header";
import RjZsWi from "./RjZsWi";
import XFooter from "./XFooter";
import PGamePasswordModalno2 from "./PGamePasswordModalno2";

export default {
  name: "MyGameList",
  components: {
    Header,
    RjZsWi,
    XFooter,
    PGamePasswordModalno2,
  },
  props: ["rJZsWIProps"],
  data() {
    return {
      myGameDt: [],
      tabs: ["", "", "", ""],
      isModalVisible: false,
      selectedTab: "",
      selectedItemId: null,
      currentPage: 1,
      itemsPerPage: 5, //  5  
      arrowLeftImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg", //    URL
      arrowRightImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg", //    URL
      items: [],
    };
  },
  computed: {
         // 24.12.18  -  :     
    visiblePages() {
      const totalPages = this.totalPages;
      const currentPage = this.currentPage;
      const maxVisiblePages = 5;

      let startPage = Math.max(1, currentPage - Math.floor(maxVisiblePages / 2));
      let endPage = startPage + maxVisiblePages - 1;

      if (endPage > totalPages) {
        endPage = totalPages;
        startPage = Math.max(1, endPage - maxVisiblePages + 1);
      }

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    filteredItems() {
      //      
      console.log("Selected Tab:", this.selectedTab);
      console.log("Filtered Items:", this.items);
      //    
      if (this.selectedTab === "") {
        return this.items;
      } else if (this.selectedTab === "") {
        return this.items.filter(item => item.type === "game");
      } else if (this.selectedTab === "") {
        return this.items.filter(item => item.type === "competition");
      } else if (this.selectedTab === "") {
        return this.items.filter(item => item.type === "ended");
      }
    },
    paginatedItems() {
      console.log("Paginated Items:", this.filteredItems.slice((this.currentPage - 1) * this.itemsPerPage, this.currentPage * this.itemsPerPage));
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredItems.slice(start, end);
    },
    totalPages() {
      console.log("Filtered Items Length:", this.filteredItems.length);
      return Math.ceil(this.filteredItems.length / this.itemsPerPage);
    },
  },
  mounted() {
    this.getMyGameUserDt();
  },
  methods: {
    async getMyGameUserDt() {
      try {
        const response = await taskStore.getSelectTasks(
          "competitions/users/me/competitions"
        );
        //if (!response || response.length === 0) {
        //  alert(".");
        //  this.$router.push("/login"); //   
        //   return;
        // }
        const today = new Date();
        today.setHours(0, 0, 0, 0); //   00:00:00   

        this.items = response.map((card) => {
          //  : "YYYY-MM-DD"  
          const formattedStartDate = card.startDate
            ? card.startDate.split("T")[0]
            : "";
          const formattedEndDate = card.endDate
            ? card.endDate.split("T")[0]
            : "";

          const startDate = new Date(formattedStartDate);
          const endDate = new Date(formattedEndDate);

          //  
          let type = "game"; // 
          if (startDate && endDate) {
            if (endDate < today) {
              type = "ended";
            } else if (startDate <= today && today <= endDate) {
              type = "competition";
            }
          }

          return {
            ...card,
            type, // "", "", ""  

            period: `${formattedStartDate} ~ ${formattedEndDate}`, //  
          };
        });

        console.log("Fetched Competitions:", this.items);
      } catch (error) {
        console.error("Failed to fetch competition data:", error);
      }
    },

    openPGameModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
    goDetailgame(item) {
      if (item.type === "ended") {
        alert("    .");
        return;
      }
      if (item.status === "PRIVATE") {
        // PRIVATE      
        this.selectedItemId = item.id;
        this.openPGameModal();
      } else {
        // PRIVATE       
        this.$router.push({
          name: "gameInfoDetail",
          params: { id: item.id, type: item.type }, // type   
        });
      }
    },
    selectTab(tab) {
      console.log("Selected Tab:", tab);
      this.selectedTab = tab;
      this.currentPage = 1; //     
    },
    goToPage(page) {
      console.log("Navigating to page:", page);
      this.currentPage = page;
    },
    prevPage() {
      if (this.currentPage > 1) {
        console.log("Going to previous page:", this.currentPage - 1);
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        console.log("Going to next page:", this.currentPage + 1);
        this.currentPage++;
      }
    },
    goDetailgame(item) {
      if (item.type === "ended") {
        alert("    .");
        return;
      }
      if (item.status === "PRIVATE") {
        // PRIVATE      
        this.selectedItemId = item.id;
        this.openPGameModal();
      } else {
        // PRIVATE       
        this.$router.push({
          name: "gameInfoDetail",
          params: { id: item.id, type: item.type }, // type   
        });
      }
    },
    handlePasswordSubmit(password) {
      //   .
      const selectedCard = this.items.find(
        (item) => item.id === this.selectedItemId
      );

      if (!selectedCard) {
        console.error("    .");
        alert("  .");
        return;
      }

      //   
      if (selectedCard.type === "ended") {
        alert("    .");
        return;
      }

      //    
      if (String(password) === String(selectedCard.passWord)) {
        // alert(" .");
        this.closeModal();
        this.$router.push({
          name: "gameInfoDetail",
          params: { id: selectedCard.id, type: selectedCard.type },
        });
      } else {
        console.error("  .");
        alert("  .");
      }
    }
    ,
    // prevPage() {
