    //     this.selectedTab === "" &&
    //     this.currentPageEnded < this.totalPages
    //   ) {
    //     this.currentPageEnded++;
    //   }
    // },
    // goToPage(page, type) {
    //   if (type === "entire") {
    //     this.currentPageEntire = page;
    //   } else if (type === "game") {
    //     this.currentPageGame = page;
    //   } else if (type === "competition") {
