import ManagerHeader from "./ManagerHeader.vue";
import ManagerNavbar from "./ManagerNavbar.vue";
import axios from "axios";
import { managerStore } from "@/store/managerStore";
export default {
  name: "ManagerGameManagement",
  components: {
    ManagerHeader,
    ManagerNavbar,
    AdminUserInfoModal,
  },
  props: ["id"],
  data() {
    return {
      gameData: null,
      isModalVisible: false,
      tabs: [" ", "  "],
      CTableHeaders: [
        "",
        "",
        "",
        "",
        "",
        "  ",
        "",
        "",
      ],
      posterUrl: "",
      selectedTab: " ",
      gameName: "",
      rank: "",
      gender: "",
      startGameDate: "",
      branchIds: ["", ""], //      
      courseManagement: ["", ""],
      courseOptions: [],
      shopOptions: [],
      endGameDate: "",
      status: "",
      mulliganCount: "",
      branchUsage: "",
      ageRange: "",
      rulesAndPrizes: "",
      concedeDistance: "",
      mulliganOptions: [1, 2, 3],
      concedeDistanceOptions: ["0m", "0.5m", "1.0m", "1.5m", "2m", "3m"],
      openOptions: ["", ""],
      startGameTime: "00:00",
      endGameTime: "00:00",
      timeOptions: this.generateTimeOptions(),
      photoUrl: "",
      users: [],
      arrowLeftImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg", //    URL
      arrowRightImage:
        "https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/iconamoon-arrow-up-2-thin.svg", //    URL
      currentPage: 1,
      itemsPerPage: 5, // Display 6 courses per page
      password: "",
      errorMessage: "", // 24.12.06  - : 
    };
  },
  computed: {
    // 24.12.12  -  :     
    visiblePages() {
      const totalPages = this.totalPages;
      const currentPage = this.currentPage;
      const maxVisiblePages = 5;

      let startPage = Math.max(1, currentPage - Math.floor(maxVisiblePages / 2));
      let endPage = startPage + maxVisiblePages - 1;

      if (endPage > totalPages) {
        endPage = totalPages;
        startPage = Math.max(1, endPage - maxVisiblePages + 1);
      }

      return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
    },
    totalPages() {
      return Math.ceil(this.users.length / this.itemsPerPage);
    },
    paginatedItems() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.users.slice(start, start + this.itemsPerPage);
    },
    filteredCourseOptions() {
      // return this.courseOptions.filter(
      //   (option) => option !== this.courseManagement[0]
      // 24.12.09 
      return this.courseOptions.filter(
        (course) => !this.courseManagement.includes(course.id)
      );
    },
  },
  watch: {
    branchIds: {
      handler(newBranchIds) {
        //     (,  select  )    
        if (
          newBranchIds.every((id) => id !== "") &&
          newBranchIds.length < this.shopOptions.length
        ) {
          this.branchIds.push("");
        }
      },
      deep: true,
    },
  },
  // created() {
  //   const id = this.$route.params.id;
  //   if (id) {
  //     this.getCompetition(id);
  //   } else {
  //     console.error("ID is missing in route params.");
  //   }
  // },
  // 24.12.09 
  async created() {
    try {
      await this.getGameshop(); // shopOptions 
      const id = this.$route.params.id;

      if (id) {
        await this.getCompetition(id); //   
        // branchIds   
        this.branchIds = this.branchIds.map((id) => String(id));
        //   
        this.shopOptions = this.shopOptions.filter(
          (shop) => shop.id !== this.selectedShopId
        );
        console.log('this.shopOptions', this.shopOptions)
      } else {
        console.error("ID is missing in route params.");
      }
    } catch (error) {
      console.error("Error during created lifecycle:", error);
    }
  },
