    getCurrentLocation() {
      console.log("[Step 1] Geolocation   ");
      this.protocol = window.location.protocol;
      const hostname = window.location.hostname;


      // HTTPS  
      if (this.protocol === 'https:' || hostname === 'localhost' || hostname === '127.0.0.1') {
        if (navigator.geolocation) {
          console.log("[Step 2] Geolocation .    ...");

          navigator.geolocation.getCurrentPosition(
            (position) => {
              console.log("[Step 3]    ");
              console.log(`[Step 4] : ${position.coords.latitude}`);
              console.log(`[Step 5] : ${position.coords.longitude}`);
              console.log(`[Step 6] : ${position.coords.accuracy} `);

              this.location = {
                lat: position.coords.latitude,
                lng: position.coords.longitude,
              };
              console.log("[Step 7]    :", this.location);

              this.loadNaverMap(); //   
              console.log("[Step 8]      ");
            },
            (error) => {
              console.error("[Error]    :", error);
            }
          );
        } else {
          console.error(
            "[Error]   Geolocation API  ."
          );
          alert("Geolocation is not supported by this browser.");
        }
      } else {
        // HTTP   
        fetch("http://ip-api.com/json/")
          .then((response) => response.json())
          .then((data) => {
            console.log("IP-based location:", data);
            this.location = {
              lat: data.lat,
              lng: data.lon,
            };
            this.loadNaverMap(); //   
          })
          .catch((error) => console.error("Error fetching location:", error));
      }




