    //    - 24.11.19 
    getShortAddress(fullAddress) {
      if (!fullAddress) {
        console.warn(
          "[getShortAddress] Received undefined or empty address:",
          fullAddress
        );
        return "  "; //   
      }

      //    (: "") 
      const regex = /(.*|.*|.*|.*|.*)/;
      const match = fullAddress.match(regex);
      return match ? match[0] : fullAddress; //     
    },
