                mapImage: closestShop.mapImage,
                managerid: closestShop.managerid,
                storeScale: closestShop.storeScale,
                location: {
                  latitude: closestShop.location.latitude,
                  longitude: closestShop.location.longitude,
                },
              },
            });
          });
        }
