      //    
      map.setCenter(new naver.maps.LatLng(
        closestShop.location.latitude,
        closestShop.location.longitude
      ));
    },
    //    
    calculateDistance(lat1, lng1, lat2, lng2) {
      const toRad = (value) => (value * Math.PI) / 180;
      const R = 6371; // Earth's radius in kilometers
      const dLat = toRad(lat2 - lat1);
      const dLng = toRad(lng2 - lng1);
      const a =
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(toRad(lat1)) *
        Math.cos(toRad(lat2)) *
        Math.sin(dLng / 2) *
        Math.sin(dLng / 2);
      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
      return R * c; // Distance in kilometers
    },

    findClosestBranch(currentLocation, branches) {
      console.log("Calculating distances from:", currentLocation); //   
      console.log("Branches to calculate:", branches); //    

      const branchesWithDistance = branches.map((branch) => {
        const distance = this.calculateDistance(
          currentLocation.lat,
          currentLocation.lng,
          branch.location.latitude,
          branch.location.longitude
        );
        console.log(`Distance to ${branch.name}: ${distance} km`); //    
        return { ...branch, distance };
      });

      const sortedBranches = branchesWithDistance.sort(
        (a, b) => a.distance - b.distance
      );
      console.log("Sorted Branches by Distance:", sortedBranches); //   

      return [
        {
          name: " ",
          location: {
            latitude: currentLocation.lat,
            longitude: currentLocation.lng,
          },
          distance: 0,
        },
        ...sortedBranches,
      ];
    },
