    //   
    loadNaverMap() {
      // Naver Maps API  
      if (typeof naver !== "undefined") {
        this.naverInitMap();
      } else {
        //    
        const checkNaverMap = setInterval(() => {
          if (typeof naver !== "undefined") {
            clearInterval(checkNaverMap);
            this.naverInitMap();
          }
        }, 100); // 100ms  
      }
