                    <div class="comment" v-for="comment in comments" :key="comment.comment_id">
                      <div class="comment-header">
                        <!-- 24.11.26  -->
                        <img class="comment-avatar" :src="comment.avatar || (comment.profileImage || basicProfileImg)"
                          alt="User avatar" />
