import { taskStore } from '@/store/taskStore';  // API 
import axios from 'axios';

export default {
  name: "ManagerLogin",
  data() {
    return {
      isModalVisible: false,
      idx: '',
      pw: '',
      userDt: [],
    };
  },
  created() { // view   
