    // this.handleSubmit(`/api/users/${id}`)
  },
  mounted() { //      .

  },
  watch() { //   

  },
  computed() { // data()    ,      (Watch  Computed     "" .) v-model   

  },
  methods: {
    // 24.11.20  -    
    async loginbutton() {
      try {
        const userInfo = {
          "phone": this.idx,
          "password": this.pw
        }

        this.userDt = await taskStore.postTask(`v1/auth/phone/login-admin`, userInfo);
        // this.userDt= await axios.post('http://3.35.157.40/api/v1/auth/phone/login-admin',
        // userInfo,
        //     {
        //       headers: {
        //         'accept': '*/*',
        //         'Content-Type': 'application/json'
        //       }
        //     });
        console.log("API  ", JSON.stringify(this.userDt))
        console.log("API  ", JSON.stringify(this.userDt[0].user))
        console.log("API  ", JSON.stringify(this.userDt[0].token))
        if (this.userDt && this.userDt[0] && this.userDt[0].token) {
          //   
          console.log(" : ", JSON.stringify(this.userDt[0].user));
          console.log(": ", JSON.stringify(this.userDt[0].token));

          localStorage.setItem('managerInfo', JSON.stringify(this.userDt[0].user));
          localStorage.setItem('managerToken', this.userDt[0].token);
          localStorage.setItem("isFirstLogin", "true");

          this.$router.push({ path: "/managerroom" });
        } else {
          // API     
          alert('  .   .');
        }
      } catch (error) {
        //  
        if (error.response) {
          //     
          alert('  .   .');
          this.id = "";
          this.pw = "";
          console.error("  : ", error.response);
        } else {
          //     
          alert("   .  .");
          console.error(" : ", error);
        }
      }
    }
  },
};
</script>
