            </button>
          </div>
        </div>
      </div>
      <!-- 24.12.19  -   -->
      <SwingVideoDeleteModal v-if="isDeleteModalVisible" @close="closeDeleteModal" @confirm="deleteSwing" />
      <x-footer />
    </div>
  </div>
