<template>
  <div class="container-center-horizontal">
    <div class="resetpassword screen">
      <Header />
      <div class="resetpassword-wrap">
        <h1 class="resetpassword-title valign-text-middle pretendard-extra-extra-bold-black-30px"> 
        </h1>
        <div class="resetpassword-content">
          <div class="resetpassword-content-item">
            <div class="resetpassword-content-item-title pretendard-medium-log-cabin-18px"></div>
            <input
              type="password"
              v-model="newPassword"
              @input="validatePassword" 
              :maxlength="20"
              placeholder=" "
              class="resetpassword-input-box valign-text-middle pretendard-medium-mountain-mist-16px"
            />
          </div>
          <div class="resetpassword-content-item">
            <div class="resetpassword-content-item-title pretendard-medium-log-cabin-18px"> </div>
            <input
              type="password"
              v-model="confirmNewPassword"
              placeholder="  "
              class="resetpassword-input-box valign-text-middle pretendard-medium-mountain-mist-16px"
            />
          </div>
          <p v-if="errorMessage" class="error-message pretendard-regular-normal-white-12px">{{ errorMessage }}</p>
          <button class="change-btn pretendard-semi-bold-white-18px" @click="handlePasswordChange"> </button>
        </div>
      </div>
      <XFooter />
      <ResetPasswordConfirmModal v-if="isModalVisible" @close="hideModal" />
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import XFooter from "./XFooter";
import ResetPasswordConfirmModal from "./ResetPasswordConfirmModal";
import axios from "axios";

export default {
  name: "ResetPassword",
  components: {
    Header,
    XFooter,
    ResetPasswordConfirmModal,
  },
  data() {
    return {
      isModalVisible: false,
      newPassword: "",
      errorMessage: "",
      confirmNewPassword: "",
      phone: this.$route.params.phone || "", 
    };
  },
  methods: {
    validatePassword() {
      //  8~20,     
      const regex = /^(?!.*[^a-zA-Z0-9])(?!.*(.)\1{2,}).{8,20}$/;
      this.pwError = !regex.test(this.newPassword)
        ? " 8~20         ."
        : null;
    },
    showModal() {
      this.isModalVisible = true;
    },
    hideModal() {
      this.isModalVisible = false;
    },
    async handlePasswordChange() {
      if (this.newPassword !== this.confirmNewPassword) {
        this.errorMessage = "   .";
        return;
      }

      try {

        //    
        const response = await axios.post(
          "https://13.124.62.68:3000/api/v1/auth/reset-password",
          {
            phone: this.phone,
            newPassword: this.newPassword,
            confirmNewPassword: this.confirmNewPassword,
          },
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        );

        //  
        if (response.status === 200) {
          console.log("  :", response.data);
          alert("  .");
          this.showModal();
        } else {
          alert("  .");
        }
      } catch (error) {
        console.error("    :", error);
        alert("    .");
      }
}
  },
};
</script>
