<template>
  <div class="container-center-horizontal">
    <div class="findpassword1-2 screen">
      <Header />
      <div class="findpassword1-wrap">
        <h1 class="findpassword1-title valign-text-middle pretendard-extra-extra-bold-black-30px"> </h1>
        <div class="findpassword1-content">
          <div class="findpassword1-content-item">
            <div class="findpassword1-content-item-title pretendard-medium-log-cabin-18px"></div>
            <input
              type="text"
              placeholder=" "
