              v-model="name"
            />
          </div>
          <div class="findpassword1-content-item">
            <div class="findpassword1-content-item-title pretendard-medium-log-cabin-18px"></div>
            <input
              type="text"
              placeholder=",  / 6 ~ 12 "
              class="findpassword1-insert-name valign-text-middle pretendard-medium-mountain-mist-16px"
              v-model="id"
            />
          </div>
          <div class="findpassword1-content-item">
            <div class="findpassword1-content-item-title pretendard-medium-log-cabin-18px"></div>
            <div class="input-btn-align">
              <input
                type="text"
                placeholder=" "
                class="findpassword1-input-box pretendard-medium-mountain-mist-16px"
                v-model="phoneNm"
              />
              <button class="find-password-cert-number-btn pretendard-medium-white-16px" @click="sendSMSPhone">
                 
              </button>
            </div>
          </div>
          <div class="findpassword1-content-item">
            <div class="findpassword1-content-item-title pretendard-medium-log-cabin-18px"></div>
            <div class="input-btn-align">
              <input
                type="text"
                placeholder=" "
                class="findpassword1-input-box pretendard-medium-mountain-mist-16px"
                v-model="verify_code"
              />
              <button class="find-password-cert-number-btn pretendard-medium-white-16px"  @click="checkPhoneNumber"> </button>
            </div>
          </div>
          <button class="find-password-btn pretendard-semi-bold-white-18px" @click="successfindpassword"  :disabled="!checkCode"   :class="{ 'disabled-btn': !checkCode }" > </button>
        </div>
      </div>
      <XFooter />
    </div>
  </div>
