<template>
    <div class="all">
        <ManagerHeader />
        <div class="wrap">
            <div class="as-container">
                <ManagerNavbar />
                <div class="wrapper">
                    <!--   -->
                    <div class="title-box">
                        <div class="pretendard-bold-normal-black-28px">A/S </div>
                    </div>
                    <div class="list-box">
                        <!-- A/S    -->
                        <div class="as-title">
                            <div class="pretendard-bold-log-cabin-30px">{{ title }}</div>
                            <div class="as-info pretendard-regular-normal-mountain-mist-20px">
                                <div class="as-info-first">
