                            <div class="as-content-st pretendard-light-granite-gray-18px">
                                <p class="ft valign-text-middle">{{ content }}</p>
                            </div>
                            <button class="list-btn pretendard-medium-white-18px" @click=" toGoList()">
                                
                            </button>
                            <div class=" reply-content">
                                <div class="reply-write">
                                    <textarea placeholder="  " class="reply-textarea"
                                        v-model="contents"></textarea>
                                    <div class="reply-buttons">
                                        <button class="cancel-button pretendard-regular-normal-white-18px">
                                            
                                        </button>
                                        <button class="submit-button pretendard-regular-normal-white-18px"
                                            @click="postComments">
                                            
                                        </button>
                                    </div>
                                </div>
                                 <!-- 24.12.10  - reply    -->
                                 <div class="reply-view">
                                    <div class="reply-count pretendard-bold-black-20px"> {{ comments.length }}</div>
                                    <!-- 24.11.22  -  -->
                                    <!--    -->
                                    <div v-if="comments.length > 0">
                                        <div class="comment" v-for="comment in comments" :key="comment.id">
                                            <div class="comment-header">
                                                <img class="comment-avatar"
                                                    src="https://cdn.animaapp.com/projects/66da512d0c22d478015e0e07/releases/66da51d8cf50153cef9952df/img/group-736@2x.png"
                                                    alt="User avatar" />
                                                <span class="comment-author pretendard-medium-mine-shaft-20px">{{
                                                    comment.comment_author
                                                    }}</span>
                                                <span class="comment-date pretendard-regular-normal-mine-shaft-18px">{{
                                                    comment.comment_createdAt
                                                        ? comment.comment_createdAt.split("T")[0]
                                                        : " "
                                                }}</span>
                                            </div>
                                            <div class="comment-body pretendard-regular-normal-granite-gray-18px">
                                                <div v-if="comment.isEditing">
                                                    <textarea type="text" v-model="comment.editedContent"
                                                        class="comment-input pretendard-regular-normal-granite-gray-18px"
